<!--环型进度条-->
<template>
  <div class="circle-style">
    <div :id="id" class="charts"></div>
  </div>
</template>

<script>
/**
 * @环型进度条
 * @author：risheng.huang
 * @参数说明：
 * circleChartsData{
 *     data:'20',    /对应的数据值(百分比)
 *     color:['#31fff1','#27ccc1'], //第一个颜色渐变0颜色和百分比数据颜色，第二个渐变1颜色
 * }  必须
 * */
import echarts from 'echarts';

export default {
  name: "bda-circle-charts",
  props: {
    circleChartsData: {
      type: Object,
      default: () => {
        return {}
      }
    },
  },
  data() {
    return {
      id: Math.random() + '_',
      circleData: [],
      series: []

    }
  },
  watch: {},
  created() {
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      this.circleData = { ...this.circleChartsData }
      let color = this.circleData.color
      // let colorDiff = new echarts.graphic.LinearGradient(0, 0, 0, 1, [
      //   { offset: 0, color: color[0], },
      //   // { offset: 0.5, color: colorItem[1], },                    //柱图渐变色
      //   { offset: 1, color: color[1], },
      // ], false);
      let data = this.circleData.data
      let chartsDom = document.getElementById(this.id);
      let charts = echarts.init(chartsDom);
      let options = {
        backgroundColor: 'transparent',
        title: {
          text: data.value + '%',
          x: 'center',
          y: '38%',
          textStyle: {
            color: color[0],
            fontSize: '30',
            fontFamily: 'Noto Sans SC',
          },

        },
        color: 'rgba(7, 91, 182,1)',

        series: [{
          type: 'pie',
          radius: ['65%', '90%'],
          itemStyle: {
            normal: {
              label: {
                show: true
              },
              labelLine: {
                show: false
              }
            }
          },
          label: {
            normal: {
              show: true,
              position: 'center'
            },
            emphasis: {
              show: false
            }
          },
          labelLine: {
            normal: {
              show: false
            }
          },

          hoverAnimation: false,
          data: [{
            value: data.value,

            itemStyle: {
              normal: {
                color: { // 完成的圆环的颜色
                  colorStops: [{
                    offset: 0,
                    color: color[0] // 0% 处的颜色
                  }, {
                    offset: 1,
                    color: color[1] // 100% 处的颜色
                  }]
                },
                label: {
                  show: false
                },
                labelLine: {
                  show: false
                }
              }
            }
          }, {
            value: 100 - data.value,
            label: {
              normal: {
                formatter: '\n\n\n' + data.name,
                textStyle: {
                  color: '#fff',
                  fontSize: 18,
                  fontFamily: 'Noto Sans SC',
                }
              }
            },
          }]
        }]
      };
      charts.setOption(options)
    }
  }
}
</script>

<style scoped lang="scss">
.circle-style {
  width: 100%;
  height: 100%;
  position: relative;

  .charts {
    width: 100%;
    height: 100%;
  }
}
</style>