<!--顶部统计切换-->
<template>
  <div
    class="tabChange-style"
    element-loading-background="rgba(0, 0, 0, 0.5)"
    element-loading-text="加载中"
  >
    <div class="swiper-container">
      <div class="swiper-slide" v-if="type === '3'">
        <div class="bar-style">
          <bda-circle-charts
            class="circle-charts"
            v-if="ywclRes"
            :circleChartsData="ywChartsConfig1"
          ></bda-circle-charts>
          <bda-circle-charts
            class="circle-charts"
            v-if="ywclRes"
            :circleChartsData="ywChartsConfig2"
          ></bda-circle-charts>
        </div>
      </div>
      <div class="swiper-slide" v-if="type === '2'">
        <div class="bar-style">
          <bda-bar-charts
            v-if="pdRes"
            :barType="barType"
            :chartsConfig="pdChartsConfig"
            :isDiffColor="true"
            :barWidth="5"
            :customizedStyle="customizedStyle"
            :legendStatus="false"
            :yAxiosDw="dw"
            :percentRange="[99, 100]"
          ></bda-bar-charts>
        </div>
      </div>
      <div class="swiper-slide" v-if="type === '1'">
        <div class="bar-style">
          <bda-bar-charts
            v-if="hdRes"
            :barType="barType"
            :chartsConfig="hdChartsConfig"
            :barWidth="5"
            :legendStatus="false"
            :yAxiosDw="dw"
            :percentRange="[60, 100]"
          ></bda-bar-charts>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Interface from '../../../base/mixin/service-index-control';
import qs from 'qs';
import bdaBarCharts from '../../../widgets/bda-3d-barCharts/bda-3d-barCharts';
import bdaCircleCharts from '../../../widgets/bda-circle-charts/bda-circle-charts';


export default {
  name: "lbTabChange",
  mixins: [Interface],
  components: { bdaBarCharts, bdaCircleCharts },
  props: {
    type: {
      type: String,
      default: '2'
    }
  },
  data() {
    return {
      customizedStyle: 0.74,//定制x轴的字体大小比例
      ywclRes: false,
      pdRes: false,
      hdRes: false,
      barType: 'stack',
      ywChartsConfig1: {},
      ywChartsConfig2: {},
      pdChartsConfig: {},
      hdChartsConfig: {},
      dw: '%',
      tabActiveIndex: 1

    }
  },
  watch: {

    tabActiveIndex(v) {
      this.$parent.tabIndex = v;
    },
    type() {
      this.initCharts()
    },
  },
  created() {
  },
  mounted() {
    this.initCharts()
  },
  methods: {
    chartsDataFilter(data, name) {
      let dataMap = {
        dimension: [],
        quotaMap: {}
      };
      dataMap.quotaMap[name] = [];
      if (data) {
        for (let key in data) {
          dataMap.dimension.push(key);
          dataMap.quotaMap[name].push(data[key])
        }
      }
      let colors1 = ['#00FFEF', '#007EFF']
      let colors2 = [['#00FEBB', '#00BDFA'], ['#00FEBB', '#00BDFA'], ['#00FEBB', '#00BDFA'], ['#00E2FF', '#0074FF'], ['#00E2FF', '#0074FF'], ['#00E2FF', '#0074FF']]
      if (name === '业务处理满意度') {
        dataMap.colorMap = { '业务处理满意度': colors1 };

        this.ywChartsConfig1 = {
          data: {
            name: '主城渝西', value: data['主城渝西'],
          },
          color: ['#00E7FF', '#0372d8']
        };
        this.ywChartsConfig2 = {
          data: {
            name: '渝东南北', value: data['渝东南北'],
          },
          color: ['#00FFBC', '#00b9fe']
        };
        window.console.log(this.ywChartsConfig, 'this.ywChartsConfig');

        this.ywclRes = false;
        this.$nextTick(() => {
          this.ywclRes = true;
        })
      }
      if (name === '回单及时率') {
        dataMap.colorMap = { '回单及时率': colors1 };
        this.hdChartsConfig = dataMap;
        window.console.log(this.hdChartsConfig, 'this.hdChartsConfig');

        this.hdRes = false;
        this.$nextTick(() => {
          this.hdRes = true;
        })
      }
      if (name === '派单及时率') {
        dataMap.colorMap = { '派单及时率': colors2 };
        this.pdChartsConfig = dataMap;
        window.console.log(this.pdChartsConfig, 'this.pdChartsConfig');

        this.pdRes = false;
        this.$nextTick(() => {
          this.pdRes = true;
        })
      }
    },
    initCharts() {
      window.console.log(this.type, 'this.type');

      if (this.type === '3') {
        //业务处理满意度
        this.getBusinessForSatisfaction(qs.stringify({
          dimensionality: this.$parent.dateType,
          timeStr: this.$parent.dateParams,
        })).then(res => {
          this.chartsDataFilter(res, '业务处理满意度');
        }).catch(() => {
          this.ywclRes = true;
        });
      }
      if (this.type === '2') {
        //派单及时率
        this.getSendOrdersTimelinessRate(qs.stringify({
          dimensionality: this.$parent.dateType,
          timeStr: this.$parent.dateParams,
        })).then(res => {
          this.chartsDataFilter(res, '派单及时率');
        }).catch(() => {
          this.pdRes = true;
        });
      }
      if (this.type === '1') {
        //回单及时率
        this.getAckOrdersTimelinessRateForType(qs.stringify({
          dimensionality: this.$parent.dateType,
          timeStr: this.$parent.dateParams,
        })).then(res => {
          let _data = {}
          if (res && res.length !== 0) {
            for (let item of res) {
              _data[item.busisubtype] = item.per
            }
          }
          this.chartsDataFilter(_data, '回单及时率');
        }).catch(() => {
          this.hdRes = true;
        });

      }
    }
  }
}
</script>

<style scoped lang="scss">
.tabChange-style {
  width: 90%;
  height: calc(100% - 30px);
  position: relative;
  // padding: 15px 0px;
  margin: auto;

  .swiper-container {
    width: 100%;
    height: 100%;
    position: relative;

    .swiper-slide {
      width: 100%;
      height: 100%;
      position: relative;
      .title {
        line-height: 35px;
        text-align: center;
        color: #fff;
        font-size: 16px;
      }

      .bar-style {
        width: 100%;
        height: 100%;
        position: relative;
        display: flex;
        .circle-charts {
          flex: 1;
        }
      }
    }
  }
}
</style>